.footer {
  padding: 54px 0;
  border-top: 1px solid $light-blue;

  &__date {
    padding: 0;
    margin: 0;
    line-height: 1;
    color: $purple;
    text-align: center;

    @include screen-below(890px) {
      display: none;
    }
  }

  &__list {
    padding: 0;
    margin: 0;
    text-align: left;
  }

  &__list-item {
    display: inline-block;
    margin-right: 20px;
    list-style-type: none;

    &:last-of-type {
      margin-right: 0;
    }
  }

  &__list-link {
    display: inline-block;
  }

  @include screen-below(1024px) {
    padding: 52px 0;
  }
}